.niviCard {
  border: 4px rgb(99, 99, 99) solid;
  border-radius: 2px;
  width: 150px;
  height: 150px;
  margin: 1rem;
  position: relative;
  -webkit-box-shadow: 4px 4px 10px rgb(56, 56, 56);
          box-shadow: 4px 4px 10px rgb(56, 56, 56);
  -webkit-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;

}

.niviCard > .img-container {
  height: 100%;
  overflow: hidden;
  background:rgb(255, 255, 255);
    
  }
.niviCard > .img-container > img {
    width: 75%
  }

.niviCard:hover {
  -webkit-box-shadow: 8px 8px 20px rgb(56, 56, 56);
          box-shadow: 8px 8px 20px rgb(56, 56, 56);
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

.niviCard:active {
  -webkit-transform: translate(1px, 1px);
      -ms-transform: translate(1px, 1px);
          transform: translate(1px, 1px);
}





